export function generatePersonData() {
  //losowanie narodowości
  const nationalitySeed = Math.random();

  if (nationalitySeed < localResidentPossibility) {
    person.nationality = borderLocationCode;
  } else {
    person.nationality = locales[Math.floor(Math.random() * locales.length)];
  }

  const nationalityData = allFakers[person.nationality]; //zmienna techniczna

  //losowanie płci
  person.gender = Math.random() > genderPossibility ? "male" : "female";

  //losowanie kraju zamieszkania
  person.countryOfResidence;
  const countryOfResidenceSeed = Math.random();

  //opisy szans poniżej trochę niedokładne ale odpowiadają kodowi
  if (countryOfResidenceSeed < localResidentPossibility) {
    //70% szansy na zamieszkanie w kraju pochodzenia
    person.countryOfResidence = person.nationality;
  } else if (countryOfResidenceSeed < borderLocationResitentPossibility) {
    //20% szansy na zamieszkanie w kraju lokalizacji punktu granicznego
    person.countryOfResidence = borderLocationCode;
  } else {
    //10% szansy na inny adres zamieszkania
    person.countryOfResidence =
      locales[Math.floor(Math.random() * locales.length)];
  }
  const countryOfResidenceData = allFakers[person.countryOfResidence]; //zmienna techniczna

  // ***Generowanie danych osobowych***

  person.firstName = nationalityData.person.firstName(person.gender);
  person.lastName = nationalityData.person.lastName(person.gender);

  //poprawne nazwiska dla polski
  try {
    if (
      person.gender == "female" &&
      person.lastName.endsWith("i") &&
      person.nationality == "pl"
    ) {
      person.lastName = person.lastName.slice(0, -1) + "a";
    }
  } catch (e) {
    console.log(e);
  }

  person.birthDate = faker.date
    .birthdate({ min: 16, max: 75, mode: "age" })
    .toLocaleDateString("pl-PL");
  person.bio = nationalityData.person.bio();

  // Logika generowania adresu
  person.nationality = countryNames[person.nationality] || "???";
  person.countryOfResidence = countryNames[person.countryOfResidence] || "???";

  try {
    person.address.street = `${countryOfResidenceData.location.streetAddress(
      true
    )}`;
    person.address.city = `${countryOfResidenceData.location.city()}`;
  } catch (e) {
    console.log("error: ", e);
    // console.log('countryOfResidence',countryOfResidence)
  }

  person.imigration = imigration(person);
  person.legit = legitness(person);

  // później należy dodać jeszcze zawód, rodzinę, - na wniosku visowym
  // przynależność do organizacji T*, nakazy aresztowania, historię podróży, kontrabanda itp

  return { person };
}

