function legitness(person) {
  const legitSeed = Math.random(); // Ziarno legitności
  const isLegit = legitSeed < 0.8; // 80% szans na legitność

  let issue = "";

  if (!isLegit) {
    const reasonSeed = Math.random(); // Ziarno problemu
    // Wybór powodu nieligityczności na podstawie tego samego ziarna
    if (reasonSeed < 0.2) {
      issue = "document issues"; // 20% szans
    } else if (reasonSeed < 0.4) {
      issue = "smuggling - often travels"; // Kolejne 20% szans
    } else if (reasonSeed < 0.6 && person.nationality !== borderLocation) {
      issue = "visa condition violation"; // Kolejne 20% szans
    } else if (reasonSeed < 0.8 && person.nationality !== borderLocation) {
      issue = "missing required information"; // Kolejne 20% szans
    } else {
      issue =
        "Wanted for " +
        wantedReasons[Math.floor(Math.random() * wantedReasons.length)]; // Pozostały % szans
    }
  }

  return { isLegit, issue };
}

function getborderLocationCountryCode(countryName) {
  for (const [key, value] of Object.entries(countryNames)) {
    if (value === countryName) {
      return key;
    }
  }
}
